import React from 'react'
import { Card, CardBody, Heading, Text } from 'uikit'
import styled from 'styled-components'
import BigNumber from 'bignumber.js'
import useI18n from 'hooks/useI18n'
import { getBalanceNumber } from 'utils/formatBalance'
import { useBurnedBalance, useTotalSupply } from 'hooks/useTokenBalance'
import { getBossAddress } from 'utils/addressHelpers'
import { useCompounderFarms, useFarms, usePriceTokenUsdt, usePriceEthUsdt, usePriceMovrUsdt, useTokenPerBlock } from 'state/hooks'
import { Pool } from 'state/types'
import { BOSS_MAX_SUPPLY } from 'config'
import CardValue from './CardValue'
import { getFarmTvl } from '../../../utils/getFarmTvl'

const StyledPefiStats = styled( Card )`
  margin-left: auto;
  margin-right: auto;
  background: ${ ( { theme } ) => theme.colors.primaryDark };
`

const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`

interface PoolWithApy extends Pool {
  apy: BigNumber
}

interface HarvestProps {
  pool: PoolWithApy
}

const PefiStats: React.FC<HarvestProps> = () => {
  const TranslateString = useI18n()
  const totalSupply = useTotalSupply()
  const burnedBalance = useBurnedBalance( getBossAddress() )
  const tokenPerBlock = useTokenPerBlock()
  const farmsLP = useFarms()
  const compounderFarms = useCompounderFarms()
  const bossPrice = usePriceTokenUsdt()
  const movrPrice = usePriceMovrUsdt()
  const ethPriceUsd = usePriceEthUsdt()

  // calculate TVL in farms
  const getFarmsTVL = () => {
    let farmsTVL = new BigNumber( 0 )
    farmsLP.map( ( farmLP ) => {
      const farmTVL = getFarmTvl( farmLP, { movrPrice, bossPrice, ethPriceUsd } )
      farmsTVL = farmsTVL.plus( farmTVL )
      return farmsTVL
    } )
    return farmsTVL.toNumber()
  }

  // calculate TVL in compounder farms
  const getCompounderFarmsTVL = () => {
    let compounderFarmsTVL = 0
    compounderFarms.map( ( compounderFarm ) => {

      if ( compounderFarm.strategyRatio ) {
        const totalValue = getFarmTvl( compounderFarm, { movrPrice, bossPrice, ethPriceUsd } )
        compounderFarmsTVL += totalValue.times( compounderFarm.strategyRatio ).toNumber()
      }

      return compounderFarm
    } )
    return compounderFarmsTVL
  }

  // get pefi marketcap
  const getBossMarketcap = () => {
    if ( bossPrice && totalSupply ) return bossPrice.toNumber() * getBalanceNumber( totalSupply )
    return 0
  }

  const tvl = getFarmsTVL() + getCompounderFarmsTVL()
  const bossMarketcap = getBossMarketcap()

  return (
    <StyledPefiStats>
      <CardBody>
        <Heading size="xl" mb="24px">
          { TranslateString( 534, 'BOSS Stats' ) }
        </Heading>
        <Row>
          <Text color="primary" fontSize="14px">
            { TranslateString( 536, 'Circulating BOSS Supply:' ) }
          </Text>
          { totalSupply && <CardValue fontSize="14px" suffix=" BOSS" value={ getBalanceNumber( totalSupply ) }/> }
        </Row>
        <Row>
          <Text color="primary" fontSize="14px">
            { TranslateString( 538, 'Total BOSS Burned:' ) }
          </Text>
          { burnedBalance && <CardValue fontSize="14px" value={ getBalanceNumber( burnedBalance ) }/> }
        </Row>
        <Row>
          <Text color="primary" fontSize="14px">
            { TranslateString( 538, 'Total Value Locked (TVL):' ) }
          </Text>
          { tvl && <CardValue fontSize="14px" prefix="$" decimals={ 2 } value={ tvl || 0 }/> }
        </Row>
        <Row>
          <Text color="primary" fontSize="14px">
            { TranslateString( 538, 'BOSS Marketcap:' ) }
          </Text>
          { bossMarketcap && <CardValue fontSize="14px" prefix="$" value={ bossMarketcap }/> }
        </Row>
        <Row>
          <Text color="primary" fontSize="14px">
            { TranslateString( 540, 'BOSS Emission Rate:' ) }
          </Text>
          <CardValue fontSize="14px" decimals={ 2 } suffix=" BOSS/block" value={ tokenPerBlock.toNumber() }/>
        </Row>
        <Row>
          <Text color="primary" fontSize="14px">
            { TranslateString( 538, 'Max BOSS Supply:' ) }
          </Text>
          <CardValue fontSize="14px" bold value={ BOSS_MAX_SUPPLY }/>
        </Row>
      </CardBody>
    </StyledPefiStats>
  )
}

export default PefiStats
