import React from 'react'
import BigNumber from 'bignumber.js'
import styled from 'styled-components'
import { getBalanceNumber } from 'utils/formatBalance'
import { Card, CardBody, Heading } from 'uikit'
import { useTotalSupply } from 'hooks/useTokenBalance'
import useI18n from 'hooks/useI18n'
import { Pool } from 'state/types'

const StyledTotalValueLockedCard = styled(Card)`
  align-items: center;
  display: flex;
  flex: 1;
  background: #d4444c;
  background: ${({ theme }) => theme.colors.primaryDark};
`
const CardMidContent = styled(Heading).attrs({ size: 'xl' })`
  line-height: 44px;
  color: ${({ theme }) => theme.colors.text};
`

const Title = styled(Heading)`
  color: #ffffff;
`

const Text = styled(Heading)`
  color: ${({ theme }) => theme.colors.text}88;
`

interface PoolWithApy extends Pool {
  apy: BigNumber
}

interface HarvestProps {
  pool: PoolWithApy
}

const PercentagePefiStakedNests: React.FC<HarvestProps> = ({ pool }) =>
{
  const totalSupply = useTotalSupply()
  const TranslateString = useI18n()

  const { totalStaked } = pool

  if (totalStaked) {
    const percentageStaked = ( getBalanceNumber(totalStaked) / getBalanceNumber(totalSupply) ) * 100
    return (
      <StyledTotalValueLockedCard>
        <CardBody>
          <Text size="md">{TranslateString(534, 'A total of')}</Text>
          <CardMidContent color="primary">
            {`${parseInt(percentageStaked.toFixed(2))}% ${TranslateString(736, 'of BOSS')}`}
          </CardMidContent>
          <Text size="md">{TranslateString(764, 'Staked in BOSS Boardroam')}</Text>
        </CardBody>
      </StyledTotalValueLockedCard>
    )
  }
  return (
    <StyledTotalValueLockedCard>
      <CardBody>
        <Title size="md">{TranslateString(762, 'Stake your BOSS now!')}</Title>
      </CardBody>
    </StyledTotalValueLockedCard>
  )
}

export default PercentagePefiStakedNests
